@import url(https://fonts.googleapis.com/css?family=Bebas+Neue|Poppins:300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{width:100%;display:flex;align-items:center;height:100px;margin-bottom:60px}.header a{margin:0 20px;text-decoration:none;color:#101010;font-size:16px;font-weight:600;font-family:'Poppins'}.container{position:relative;width:1280px;min-width:1280px;margin:0 auto;display:flex;justify-content:center}.container .page{position:absolute}.container .page a{text-decoration:none}.container .page a h2{font-family:'Bebas Neue';font-size:48px;color:#101010}.container .page .inner{display:flex;justify-content:center}.container .page .inner h1{font-family:'Bebas Neue';font-size:52px;letter-spacing:0.1rem}.container .page .inner h1 .line-wrap{overflow:hidden;height:66px}.container .page .inner .inner-info{display:flex;flex-direction:column}.container .page .inner .inner-info img{margin:0 auto;height:380px;width:280px}.container .page .inner .inner-info p{font-family:'Poppins';margin-top:80px;width:340px;font-weight:300;line-height:24px;font-style:14px}.page-enter{opacity:0}.page-enter-active{opacity:1;transition:opacity 400ms;transition-delay:600ms}.page-exit{opacity:1}.page-exit-active{opacity:0;transition:opacity 400ms}.work-inner{display:flex;flex-direction:column;justify-content:center;text-align:center}.work-inner .work-container{display:flex;justify-content:center;flex-direction:row;margin:10% 0}.work-inner .work-container .work-desc{display:flex;flex-direction:column;justify-content:center;width:100%}.work-inner .work-container .img-container img{margin-left:10%;width:764px;height:394px}.form-inner{margin:0;display:flex;flex-direction:column;justify-content:center;text-align:center;height:642px}.form-inner #nest-messages{margin:30px 0}.footer{text-align:center;margin-top:100px}.footer a{padding:5px}

